@import "base";

@page {
  margin: 0;
  margin-bottom: 2rem;
  size: Letter;
}

@page :left {
  @bottom-left {
    content: counter(page);
    position: absolute;
    z-index: -1;
    padding-bottom: 2em;
    padding-left: 2em;
  }
  @bottom-right {
    content: string(heading);
    position: absolute;
    z-index: -1;
  }
}

@page :right {
  @bottom-left {
    content: string(heading);
    position: absolute;
    z-index: -1;
  }
  @bottom-right {
    content: counter(page);
    position: absolute;
    z-index: -1;
    padding-bottom: 2em;
    padding-right: 2em;
  }
}

@page full {
  @bottom-right {
    content: none;
  }

  @bottom-left {
    content: none;
  }

  background: black;
  margin: 0;
}

@page :blank {
  @bottom-right {
    content: none;
  }

  @bottom-left {
    content: none;
  }
}

@page clean {
  @bottom-right {
    content: none;
  }

  @bottom-left {
    content: none;
  }
}

* {
  margin: 0;
  padding: 0;
}

html { counter-reset: h2-counter; }
h2 { counter-increment: h2-counter; }
body {
  font-family: 'Roboto', sans-serif;
  color: #333;
}

.full-page {
  height: 11in;
  break-after: always;
}

.front-cover {
  background: linear-gradient(315deg, #EFC237, #AD6CDC);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: 4rem;
}

#contents {
  page: clean;
}
#contents p {
  font-size: 2em;
}
#contents ul {
  display: block;
  margin: 1em 0;
  padding: 0;
}
#contents li {
  display: block;
}
#contents a {
  color: inherit;
  text-decoration: none;
}
#contents a::before {
  content: target-counter(attr(href), h2-counter) '. ' target-text(attr(href));
}
#contents a::after {
  content: target-counter(attr(href), page);
  float: right;
}

.section, ul, ol, p, h1, h2, h3, h4, h5, h6 {
  break-inside: avoid;
}

.header-image {
  width: 100%;
  height: auto;
  margin: 0;
}

.print-content {
  margin: 2em;
}

.flavor-text {
  background-color: #DDD;
  padding: 1rem;
  margin: 1rem;
  font-style: italic;
  margin-bottom: 1em;
}




